import { getParent, destroy, clone } from 'mobx-state-tree';

function changeLowLayersVisibility(layers, selectedLayer, visible) {
  layers.forEach((layer, index) => {
    if (index < selectedLayer) layer.visible = visible;
  });
}
export default self => {
  function getLayersPositions() {
    return self.layers
      .map(({ positions }) => positions.map(({ i }) => Number(i)))
      .reduce((accum, item) => accum.concat(item), []);
  }
  function addLayer() {
    self.layers.push({
      dimension: 3,
      positions: []
    });
    self.selectedLayer = self.layers.length - 1;
  }
  function remove() {
    getParent(self, 2).removeMosaic(self);
  }

  function openConfirmRemoveMosaic() {
    getParent(self, 2).openConfirmRemoveMosaic(self);
  }
  function removeLayer(item) {
    if (self.layers.length > 1) {
      destroy(item);
      if (self.selectedLayer > 0) {
        self.selectedLayer -= 1;
      }
    }
  }
  function selectLayer(index) {
    self.selectedLayer = index;
  }
  function getNewtPositionNum() {
    const layersPositions = getLayersPositions();

    const lastPosition = Math.max(...layersPositions) + 1;
    return layersPositions.length ? lastPosition.toString() : '1';
  }
  function renameLayersPositions(layers, positionNumResolver) {
    return layers.map(({ positions, ...other }) => ({
      ...other,
      positions: positions
        .map(({ i, ...other }) => ({
          ...other,
          i: positionNumResolver(i)
        }))
        .sort((prev, curr) => prev.i - curr.i)
    }));
  }
  function remappingPositionsNum() {
    const layersPositions = getLayersPositions().sort((prev, curr) => prev - curr);

    const renamePositionNum = {};
    layersPositions.forEach((i, index) => {
      renamePositionNum[i] = `${index + 1}`;
    });
    const positionNumResolver = currNum => renamePositionNum[currNum];
    self.layers = renameLayersPositions(self.layers, positionNumResolver);
  }
  function changePositionNum(selectedPosition, newNum) {
    function renamePosition(selectedPosition, newNum, currNum) {
      if (currNum === selectedPosition) return newNum;
      if (currNum === newNum) return selectedPosition;
      return currNum;
    }
    const positionNumResolver = currNum => renamePosition(selectedPosition, newNum, currNum);
    self.layers = renameLayersPositions(self.layers, positionNumResolver);
  }
  function changePositionRole(selectedPosition, newRole) {
    function roleResolver(i, selectedPosition, role, newRole) {
      if (i === selectedPosition) return newRole;
      if ((newRole === 1 && role === 1) || (newRole === 2 && role === 2)) return 0;
      return role;
    }

    function renamePositionsRoles(layers, layerIndex) {
      return layers[layerIndex].positions.map(({ role, i, ...other }) => ({
        ...other,
        i,
        role: roleResolver(i, selectedPosition, role, newRole)
      }));
    }
    self.layers.forEach(({ positions, dimension }, index) => {
      self.layers[index].positions = renamePositionsRoles(self.layers, index);
    });
  }
  function changeLayerDimension(oldDimension, dimension) {
    const positions = self.layers[self.selectedLayer].positions;
    const integralPart = dimension / oldDimension;
    self.layers[self.selectedLayer] = {
      dimension,
      positions: positions.map(({ i, x, y, h, w, role }) => ({
        i,
        x: x * integralPart,
        y: y * integralPart,
        h: h * integralPart,
        w: w * integralPart,
        role
      }))
    };
  }
  function hideLowLayers() {
    changeLowLayersVisibility(self.layers, self.selectedLayer, false);
    self.lowLayersVisibility = false;
  }
  function showLowLayers() {
    changeLowLayersVisibility(self.layers, self.selectedLayer, true);
    self.lowLayersVisibility = true;
  }
  function changeLayerNum(layerIndex) {
    const selectedLayer = clone(self.layers[self.selectedLayer]);
    const changedLayer = clone(self.layers[layerIndex]);
    self.layers.set(layerIndex, selectedLayer);
    self.layers.set(self.selectedLayer, changedLayer);
    self.selectedLayer = layerIndex;
  }
  return {
    removeLayer,
    addLayer,
    remove,
    selectLayer,
    getNewtPositionNum,
    remappingPositionsNum,
    changePositionNum,
    changePositionRole,
    changeLayerDimension,
    openConfirmRemoveMosaic,
    hideLowLayers,
    showLowLayers,
    changeLayerNum
  };
};
