import React from 'react';
import { render } from 'react-dom';
import { reaction } from 'mobx';
import { clone, setLivelynessChecking } from 'mobx-state-tree';
import makeInspectable from 'mobx-devtools-mst';
import Root from 'components/Root';
import MosaicList from 'model/mosaicList/MosaicList';
import EditableMosaic from 'model/editableMosaic';
import PresetVisible from 'model/presets/PresetVisible';
import PositionModal from 'model/positionModal';
import PresetsPage from 'model/presets/PresetsPage';
import ActionMessage from 'model/actionMessage';
import Intl from 'containers/Intl';
import initialState from './initialState';

setLivelynessChecking('ignore');

const initialEditableMosaic = {
  isConfirmRemoveLayer: false,
  isConfirmRemovePositions: false,
  dimensions: [1, 2, 3, 4, 5, 6, 8, 9, 10, 12],
  selectedPosition: '',
  mosaic: {
    id: '1000',
    layers: [
      {
        dimension: 3,
        positions: []
      }
    ]
  }
};
const initialPresetsVisibles = {
  presetsOnPage: 8,
  defaultVisible: true,
  customVisible: true
};
const initialPresetsPage = { page: 0 };
const initialActionMessage = { isActionMessage: false };

const editableMosaic = EditableMosaic.create(initialEditableMosaic);
const mosaic = MosaicList.create(initialState);
const presets = PresetVisible.create(initialPresetsVisibles);
const positionModal = PositionModal.create({ visible: false, target: {} });
const defaultPresetsPage = PresetsPage.create(initialPresetsPage);
const customPresetsPage = PresetsPage.create(initialPresetsPage);
const actionMessage = ActionMessage.create(initialActionMessage);

makeInspectable(editableMosaic);
makeInspectable(mosaic);
makeInspectable(presets);
makeInspectable(positionModal);
makeInspectable(defaultPresetsPage);
makeInspectable(customPresetsPage);

mosaic.loadCustomPresets();

reaction(
  () => mosaic.isLoadError,
  isLoadError => {
    if (isLoadError) {
      actionMessage.setActionMessage(true, false, 'errorLoadingCreatedLayouts');
    }
  }
);
reaction(
  () => mosaic.isSaved,
  isSaved => {
    if (isSaved) {
      actionMessage.setActionMessage(true, true, 'layoutSuccessfullyAdded');
    }
  }
);
reaction(
  () => mosaic.isSaveError,
  isSaveError => {
    if (isSaveError) {
      actionMessage.setActionMessage(true, false, 'errorAddingLayout');
    }
  }
);
reaction(
  () => mosaic.isDelete,
  isDelete => {
    if (isDelete) {
      actionMessage.setActionMessage(true, true, 'layoutSuccessfullyDeleted');
    }
  }
);
reaction(
  () => mosaic.isDeleteError,
  isDeleteError => {
    if (isDeleteError) {
      actionMessage.setActionMessage(true, false, 'errorDeletingLayout');
    }
  }
);
reaction(
  () => actionMessage.isActionMessage,
  isActionMessage => {
    if (!isActionMessage) {
      mosaic.isLoadedOff();
      mosaic.isLoadErrorOff();
      mosaic.isSavedOff();
      mosaic.isSaveErrorOff();
    }
  }
);
reaction(
  () => mosaic.selectedMosaic,
  selectedMosaic => {
    if (selectedMosaic) {
      editableMosaic.setMosaic(clone(mosaic.getPresetById(selectedMosaic.toString())));
    }
  }
);
reaction(
  () => presets.customVisible,
  customVisible => {
    const presetsOnPage = presets.presetsOnPage;
    const page = defaultPresetsPage.page;
    const presetsLength = mosaic.getDefaultPresetsLength;
    const newPage = Math.floor(presetsLength / presetsOnPage);
    if (!customVisible) {
      if (page > 0) {
        if (presetsOnPage >= presetsLength % presetsOnPage) {
          defaultPresetsPage.setPage(newPage);
        }
        if (presetsLength % presetsOnPage === 0) {
          defaultPresetsPage.pagePrev();
        }
      }
    }
  }
);
reaction(
  () => presets.defaultVisible,
  defaultVisible => {
    const presetsOnPage = presets.presetsOnPage;
    const page = customPresetsPage.page;
    const presetsLength = mosaic.getCustomPresetsLength;
    const newPage = Math.floor(presetsLength / presetsOnPage);
    if (!defaultVisible) {
      if (page > 0) {
        if (presetsOnPage >= presetsLength % presetsOnPage) {
          customPresetsPage.setPage(newPage);
        }
        if (presetsLength % presetsOnPage === 0) {
          customPresetsPage.pagePrev();
        }
      }
    }
  }
);
reaction(
  () => mosaic.getCustomPresetsLength,
  customPresetsLength => {
    const presetsOnPage = presets.presetsOnPage;
    const page = customPresetsPage.page;
    if (
      page > 0 &&
      page === customPresetsLength / presetsOnPage &&
      customPresetsLength % presetsOnPage === 0
    ) {
      customPresetsPage.pagePrev();
    }
  }
);

render(
  <Intl>
    <Root
      {...{
        mosaic,
        presets,
        editableMosaic,
        positionModal,
        defaultPresetsPage,
        customPresetsPage,
        actionMessage
      }}
    />
  </Intl>,
  document.getElementById('root')
);
