import React from 'react';
import EditBlock from 'containers/EditBlock';
import PresetsBlock from 'containers/PresetsBlock';
import PositionModal from 'containers/PositionModal';
import SettingsBlock from 'containers/SettingsBlock';
import ConfirmRemoveMozaic from 'containers/ConfirmRemoveMozaic';
import ConfirmMosaicChanges from 'containers/ConfirmMosaicChanges';
import ActionMessage from 'containers/ActionMessage';
import styles from './styles';

const Root = ({
  mosaic,
  presets,
  editableMosaic,
  positionModal,
  defaultPresetsPage,
  customPresetsPage,
  actionMessage
}) => (
  <div style={styles.wrapper}>
    <PresetsBlock
      {...{
        editableMosaic,
        mosaic,
        presets,
        defaultPresetsPage,
        customPresetsPage,
        actionMessage
      }}
    />
    <EditBlock {...{ mosaic, editableMosaic, positionModal }} />
    <SettingsBlock {...{ editableMosaic, mosaic }} />
    <PositionModal {...{ positionModal, editableMosaic }} />
    <ConfirmRemoveMozaic mosaic={mosaic} />
    <ConfirmMosaicChanges {...{ mosaic, editableMosaic }} />
    <ActionMessage actionMessage={actionMessage} />
  </div>
);
export default Root;
