import sizeMe from 'react-sizeme';
import { injectIntl } from 'react-intl';

import { observer, inject } from 'mobx-react';
import { compose, withHandlers, withProps } from 'recompose';

import isEqual from 'lodash.isequal';

function calcWidth(width) {
  const presetMargin = (width / 100) * 1.2;
  return {
    presetMargin,
    prstCntnrWidth: width,
    itemMargin: width / 100,
    presetWidth: width / 4 - 2 * presetMargin
  };
}
function checkMosaicChanges(mosaic, selectedMosaic, getPresetById) {
  if (selectedMosaic) {
    const selectedLayers = getPresetById(selectedMosaic.toString()).layers.map(
      ({ positions, dimension }) => ({ positions, dimension })
    );
    const savedLayers = mosaic.layers.map(({ positions, dimension }) => ({ positions, dimension }));
    return isEqual(savedLayers, selectedLayers);
  }
  return (
    mosaic.layers.length === 1 &&
    mosaic.layers[0].positions.length === 0 &&
    mosaic.layers[0].dimension === 3
  );
}
export default compose(
  injectIntl,
  sizeMe(),
  inject('editableMosaic', 'mosaic', 'presets', 'defaultPresetsPage', 'customPresetsPage'),
  observer,
  withHandlers({
    changeDefaultVisibleHandler: ({ presets: { changeDefaultVisible } }) => () => {
      changeDefaultVisible();
    },
    changeCustomVisibleHandler: ({ presets: { changeCustomVisible } }) => () => {
      changeCustomVisible();
    },
    confirmSelectMosaic: ({
      editableMosaic: { mosaic },
      mosaic: {
        selectedMosaic,
        selectMosaic,
        openConfirmSelectMosaic,

        getPresetById
      }
    }) => (id, isDefault) => {
      const isMosaicChanged = checkMosaicChanges(mosaic, selectedMosaic, getPresetById);
      if (isMosaicChanged) {
        selectMosaic(id, isDefault);
      } else {
        openConfirmSelectMosaic(id, isDefault);
      }
    }
  }),
  observer,
  withProps(
    ({
      size: { width },
      mosaic: { selectedMosaic, defaultPresets, customPresets, selectMosaic, isDefaultSelected },
      presets: { defaultVisible, customVisible, presetsOnPage },
      defaultPresetsPage,
      customPresetsPage
    }) => {
      const { presetMargin, prstCntnrWidth, itemMargin, presetWidth } = calcWidth(width);
      const selectedDefaultMosaic = isDefaultSelected ? selectedMosaic : null;
      const selectedCustomMosaic = isDefaultSelected ? null : selectedMosaic;
      return {
        selectedMosaic,
        defaultPresets,
        customPresets,
        selectMosaic,
        defaultVisible,
        customVisible,
        presetsOnPage,
        selectedDefaultMosaic,
        selectedCustomMosaic,
        presetMargin,
        prstCntnrWidth,
        itemMargin,
        presetWidth,
        defaultPresetsPage,
        customPresetsPage
      };
    }
  )
);
