import { observer } from 'mobx-react';
import { compose, withHandlers } from 'recompose';
import { injectIntl } from 'react-intl';

export default compose(
  injectIntl,
  withHandlers({
    selectLayerHandler: ({ selectLayer }) => (event, index) => {
      selectLayer(index);
    }
  }),
  observer
);
